.footer {
  width: 100%;
  min-height: 100px;
  display: flex;
  justify-content: center;
  background-color: #2a2c2f;
  color: white;
  flex-shrink: 1;
}

.footer__container {
  max-width: 1280px;
  margin: 0;
  padding: 18px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

@media (max-width: 1200px) {
  .footer__container {
    padding-right: 48px;
    padding-left: 48px;
  }
}

@media (max-width: 950px) {
  .footer__container {
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 88px;
  }
}

@media (max-width: 767px) {
  .footer__container {
    flex-direction: column;
    align-items: flex-start;
    padding: 64px 16px 24px 16px;
  }
}

.footer__copyright {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
}

@media (max-width: 767px) {
  .footer__copyright {
    line-height: 1;
  }
}
